<template>
  <div id="app" ref="app">
    <!-- <button class="btn">切换</button> -->
    <div class="webcamOld">
      <video playsinline=true 
        class="webcamOld-video"
        ref="videoSrc"
        :width="videoWidth"
        :height="videoHeight"
        v-on:canplay="onSourceReady"
      >Your browser does not support this application.</video>
      <canvas ref="canvasOld" class="webcam-canvas" :width="videoWidth" :height="videoHeight"></canvas>
    </div>
    <p class="msgOld">{{msg}}</p>
  </div>
</template>

<script>
import Stats from 'stats.js'

const publicPath = process.env.BASE_URL
let cv = null

export default {
  name: 'app',
  data() {
    return {
      videoWidth: window.innerWidth,
      videoHeight: window.innerHeight,
      stream: null,
      stats: null,
      canvasCtx: null,
      faceClassifier: null,
      detectMat: null,
      videoCap: null,
      faceVect: null,
      processTimer: null,
      msg: ''
    }
  },
  methods: {
    startCamera() {
        const constraints = {
            'qvga': {width: {exact: 640}, height: {exact: 480}},
            'vga': {width: {exact: 640}, height: {exact: 480}},
             advanced: [
                { focusMode: 'manual', focusDistance: 100.33 }
             ]
        }
        let videoConstraint = constraints['qvga'];
        if (!videoConstraint) {
            videoConstraint = true;
        }
        let that = this;
        if (navigator.mediaDevices) {
            navigator.mediaDevices.getUserMedia({video: videoConstraint, audio: false})
            .then(function(stream) {
              that.setMsg('getUserMedia success')
              that.stream = stream
              that.$refs.videoSrc.srcObject = stream
              that.$refs.videoSrc.play()
            })
            .catch(function(err) {
                console.log('Camera Error: ' + err.name + ' ' + err.message);
            });
        }
        else if(navigator.getUserMedia) {
            alert ("Only support getUserMedia");
        }
        else {
            alert ("Media device not supported");
        }
    },
    stopCamera() {
      if (!this.stream) return
      this.setMsg('stopCamera')
      this.stopVideoProcessing()
      this.$refs.videoSrc.pause()
      this.$refs.videoSrc.srcObject = null
      this.stream.getVideoTracks()[0].stop()
      this.stream = null
    },
    onSourceReady() {
      this.setMsg('onSourceReady.')
      this.videoWidth = this.$refs.videoSrc.videoWidth
      this.videoHeight = this.$refs.videoSrc.videoHeight

      this.canvasCtx = this.$refs.canvasOld.getContext('2d')

      console.log("get window size " + window.innerWidth + " : " + window.innerHeight);
      this.startVideoProcessing()

      //disable opencv load
      // if (window.cv) {
      //   console.log(this.videoWidth, this.videoHeight)
      //   cv = window.cv
      //   this.startVideoProcessing()
      // } else {
      //   this.loadOpenCv()
      // }
    },
    initStats() {
      this.stats = new Stats()
      this.stats.showPanel(0)
      this.$refs.app.appendChild(this.stats.dom)
    },
    loadOpenCv() {
      if (!window.WebAssembly) {
        this.setMsg("Your web browser doesn't support WebAssembly.", 'warn')
        return
      }
      this.setMsg('loading OpenCv.js')
      const script = document.createElement('script')
      script.type = 'text/javascript'
      script.async = 'async'
      script.src = `${publicPath}libs/opencv.js`
      document.body.appendChild(script)
      script.onload = () => {
        this.setMsg('OpenCV.js is loaded.')
      }

      window.Module = {
        wasmBinaryFile: `${publicPath}libs/opencv_js.wasm`, // for wasm mode
        preRun: () => {
          this.setMsg('loading haarcascade_frontalface_default.xml')
          window.Module.FS_createPreloadedFile(
            '/',
            'haarcascade_frontalface',
            `${publicPath}data/haarcascade_frontalface_default.xml`,
            true,
            false
          )
        },
        _main: () => {
          this.setMsg('OpenCV.js is ready.')
          cv = window.cv
          // console.log(cv.getBuildInformation())
          this.startVideoProcessing()
        }
      }
    },

    startVideoProcessing() {
      // this.setMsg('startVideoProcessing.')


      if (!this.stream) {
        this.setMsg('Please startup your webcam.', 'warn')
        return
      }
      // let that = this;
      // setTimeout(function(){
      //   that.canvasCtx.drawImage(that.$refs.videoSrc, 0, 0, that.videoWidth, that.videoHeight);
      //   // let imgData = that.canvasCtx.getImageData(0, 0, that.videoWidth * 0.2, that.videoHeight * 0.2);
      //   // console.log("image data " + imgData.data);
      //   that.startVideoProcessing();
      //   // console.log('test');
      // }, 30);

      //disable opencv function
      // this.$refs.videoSrc

      // this.faceClassifier = new cv.CascadeClassifier()
      // this.faceClassifier.load('haarcascade_frontalface')
      // this.srcMat = new cv.Mat(this.videoHeight, this.videoWidth, cv.CV_8UC4)
      // this.detectMat = new cv.Mat()
      // this.videoCap = new cv.VideoCapture(this.$refs.videoSrc)
      // this.faceVect = new cv.RectVector()
      // this.initStats()
      // this.processTimer = requestAnimationFrame(this.processVideo)
    },

    stopVideoProcessing() {
      cancelAnimationFrame(this.processTimer)
      // // this.stats.end()
      // if (this.faceClassifier && !this.faceClassifier.isDeleted()) {
      //   this.faceClassifier.delete()
      // }
      // this.srcMat.delete()
      // this.detectMat.delete()
      // this.faceVect.delete()
      // this.canvasCtx.clearRect(0, 0, this.videoWidth, this.videoHeight)
    },

    processVideo() {
      if (!this.$refs.videoSrc) {
        this.setMsg('Video stream not found.', 'warn')
        return
      }
      this.stats.begin()
      this.videoCap.read(this.srcMat)
      cv.cvtColor(this.srcMat, this.detectMat, cv.COLOR_RGBA2GRAY, 0)
      cv.pyrDown(this.detectMat, this.detectMat)
      cv.pyrDown(this.detectMat, this.detectMat)
      this.faceClassifier.detectMultiScale(
        this.detectMat,
        this.faceVect,
        1.1,
        3,
        0
      )
      this.drawFace()
      // this.stats.end()
      this.processTimer = requestAnimationFrame(this.processVideo)
    },

    drawFace() {
      this.canvasCtx.clearRect(0, 0, this.videoWidth, this.videoHeight)
      for (let i = 0; i < this.faceVect.size(); ++i) {
        const rect = this.faceVect.get(i)
        if (rect.width > 0 && rect.height > 0) {
          this.canvasCtx.lineWidth = 2
          this.canvasCtx.strokeStyle = 'red'
          this.canvasCtx.strokeRect(
            rect.x * 4,
            rect.y * 4,
            rect.width * 4,
            rect.height * 4
          )
        }
      }
    },
    setMsg(msg, type = 'log') {
      this.msg = msg
      console[type](msg)
    }
  },
  mounted() {
    console.log("current url : "  + process.env.VUE_APP_API_URL);
    this.startCamera()
  },
  beforeDestroy() {
    console.log("destroy")
    this.stopCamera()
  }
}
</script>

<style lang="less">
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  min-height: 100vh;
  display: flex;
  // flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}
.btn {
  margin: 20px;
  padding: 5px 20px;
}
.webcamOld {
    box-sizing: border-box;
      // height: 100%;
    overflow: hidden;
    width: 100%;
    height: 100%;
    position: relative;
    transform: rotateY(180deg);
  &-video {
    // visibility: hidden;
    width: 100%;
    height:100%;
    object-fit: cover;
  }
  &-canvas {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height:100%;
    object-fit: fill;
    // height:100%;
  }
}
.msgOld {
  position: fixed;
  top: 50px;
  right: 0;
  padding: 10px;
  text-align: right;
  background: rgba(255, 255, 255, 0.3);
  font-size: 20px;
}
</style>
