<template>
  <div id="app" ref="app">
    <!-- <button class="btn">切换</button> -->
    <div class="webcam">
      <video  playsinline=true 
        class="webcam-video"
        ref="videoSrc"
        :width="videoWidth"
        :height="videoHeight"
        v-on:canplay="onSourceReady"
      >Your browser does not support this application.</video>
      <canvas ref="canvasVideo" class="webcam-canvas" :width="videoWidth" :height="videoHeight"></canvas>
    
      <canvas ref="lenCanvas"></canvas>
    </div>
    <p class="msg">{{msg}}</p>
  </div>
</template>

<script>
import Stats from 'stats.js'

const publicPath = process.env.BASE_URL

export default {
  name: 'app',
  data() {
    return {
      videoWidth: 1920,
      videoHeight: 1080,
      stream: null,
      stats: null,
      canvasCtx: null,
      msg: '',
      canvasTimer :null
    }
  },
  methods: {
    startCamera() {
        const constraints = {
            'qvga': {width: {exact: 640}, height: {exact: 480}},
            'vga': {width: {exact: 640}, height: {exact: 480}},
             advanced: [
                { focusMode: 'manual', focusDistance: 100.33 }
             ]
        }
        let videoConstraint = constraints['qvga'];
        if (!videoConstraint) {
            videoConstraint = true;
        }
        let that = this;
        if (navigator.mediaDevices) {
            navigator.mediaDevices.getUserMedia({video: videoConstraint, audio: false})
            .then(function(stream) {
              that.setMsg('getUserMedia success')
              that.stream = stream
              that.$refs.videoSrc.srcObject = stream
              that.$refs.videoSrc.play()
            })
            .catch(function(err) {
                console.log('Camera Error: ' + err.name + ' ' + err.message);
            });
        }
        else if(navigator.getUserMedia) {
            alert ("Only support getUserMedia");
        }
        else {
            alert ("Media device not supported");
        }
    }
    ,
    stopCamera() {
      if (!this.stream) return
      this.setMsg('stopCamera')
      this.stopVideoProcessing()
      this.$refs.videoSrc.pause()
      this.$refs.videoSrc.srcObject = null
      this.stream.getVideoTracks()[0].stop()
      this.stream = null
    },
    onSourceReady() {
      this.setMsg('onSourceReady.')
      this.videoWidth = this.$refs.videoSrc.videoWidth
      this.videoHeight = this.$refs.videoSrc.videoHeight
      this.canvasCtx = this.$refs.canvasVideo.getContext('2d')

      console.log(this.videoWidth, this.videoHeight)
      this.startVideoProcessing()
    },
    initStats() {
      this.stats = new Stats()
      this.stats.showPanel(0)
      this.$refs.app.appendChild(this.stats.dom)
    },
    loadOpenCv() {
      if (!window.WebAssembly) {
        this.setMsg("Your web browser doesn't support WebAssembly.", 'warn')
        return
      }
      if(this.$store.state.lensCv) {//cached
        console.log("cached Done");
        this.createTemplateMatch();
      }
      else {
        this.setMsg('loading templateMatch.js')
        const script = document.createElement('script')
        script.type = 'text/javascript'
        script.async = 'async'
        script.src = `${publicPath}libs/templateMatch.js`
        document.body.appendChild(script)
        script.onload = () => {
          this.setMsg('templateMatch.js is loaded.')
        }

        //init wasm
        window.Module = {
          wasmBinaryFile: `${publicPath}libs/templateMatch.wasm`
        }
        let that = this;
        //template match 
        window.Module.onRuntimeInitialized = function() {
            console.log("Done");
            that.$store.state.lensCv = window.Module;
            that.createTemplateMatch();
        };
      }
    },
    createTemplateMatch() {
      let imgDataTemplate  = this.$store.state.lensCv.createTemplate(60, false);//return black and white pattern
      // console.log("imageDataTemplate == " +  imgDataTemplate);
      this.loadTemplateMatchPattern(imgDataTemplate, 60)
    }
    ,
    loadTemplateMatchPattern(arrImgData, imageSizeTemplate) {
        let cvn = this.$refs.lenCanvas;
        let ctx = cvn.getContext("2d");  
        
        let arrImagedata = [];

        // defaul rgba 4 channel in js
        for (let i = 0; i < imageSizeTemplate * imageSizeTemplate; i ++) {
          arrImagedata.push(arrImgData[i]);//r
          arrImagedata.push(arrImgData[i]);//g
          arrImagedata.push(arrImgData[i]);//b
          arrImagedata.push(255);//a
        }

        cvn.width = imageSizeTemplate;
        cvn.height = imageSizeTemplate;
        
        // create the imageData object, you'll need the width and height of your image
        let dataImage = ctx.createImageData(imageSizeTemplate, imageSizeTemplate);
        // browsers supporting TypedArrays
        if (dataImage.data.set) {
            dataImage.data.set(arrImagedata);
        } else {
            // IE9
            arrImagedata.forEach(function(val, i) {
                dataImage.data[i] = val;
            });
        }
        ctx.putImageData(dataImage, 0,0);
    }
    ,
    startVideoProcessing() {
      // this.setMsg('startVideoProcessing.')

      if (!this.stream) {
        this.setMsg('Please startup your webcam.', 'warn')
        return
      }
      // this.initStats()
      let that = this
      this.canvasTimer = setTimeout(function(){
        that.canvasCtx.drawImage(that.$refs.videoSrc, 0, 0, that.videoWidth, that.videoHeight);
        // let imgData = that.canvasCtx.getImageData(0, 0, that.videoWidth * 0.2, that.videoHeight * 0.2);
        // console.log("image data " + imgData.data);
        that.startVideoProcessing()
        // console.log('test');
      }, 30);
    },

    stopVideoProcessing() {
      cancelAnimationFrame(this.processTimer)
    },

    processVideo() {
      if (!this.$refs.videoSrc) {
        this.setMsg('Video stream not found.', 'warn')
        return
      }
      this.stats.begin()
    },
    setMsg(msg, type = 'log') {
      this.msg = msg
      console[type](msg)
    }
  },
  mounted() {
    console.log("current url : "  + process.env.VUE_APP_API_URL + " : " + this.$store.state.title);
    this.startCamera()
    this.loadOpenCv()
  },
  beforeDestroy() {
    this.stopCamera()
    clearInterval(this.canvasTimer)
  }
}
</script>

<style lang="less">
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  min-height: 100vh;
  display: flex;
  // flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}
.btn {
  margin: 20px;
  padding: 5px 20px;
}
.webcam {
  position: relative;
  width: 100%;
  transform: rotateY(180deg);
  &-video {
    visibility: hidden;
    display: block;
    width: 100%;
    height: auto;
  }
  &-canvas {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }
}
.msg {
  position: fixed;
  top: 50px;
  right: 0;
  padding: 10px;
  text-align: right;
  background: rgba(255, 255, 255, 0.3);
  font-size: 20px;
}
</style>
